.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: #0c121c;
  color: white;
}

.logo-container {
  width: 100%;
  margin: auto;
  text-align: center;
}

.central-logo {
  max-width: 21%;
  margin: auto;
}

.chat-window {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.message-row {
  display: flex;
  margin-bottom: 15px;
}

/* User moves to the right */
.message-row.user {
  justify-content: flex-end;
}

/* Bot stays on the left */
.message-row.bot {
  justify-content: flex-start;
}

.bubble {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 15px;
  font-family: Arial, sans-serif;
}

.user .bubble {
  background-color: #0b93f6;
  color: white;
}

.bot .bubble {
  background-color: #444654;
  color: white;
}

.input-area {
  padding: 20px;
  display: flex;
  gap: 10px;
  background: #40414f;
}

input {
  flex: 1;
  padding: 12px;
  border-radius: 5px;
  border: none;
  outline: none;
}

button {
  padding: 10px 20px;
  background: #10a37f; /* ChatGPT Green */
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}